@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ballet&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1f2f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    /* background-color: #f1f2f5; */
    min-height: 100vh;
    min-width: 1346px;
    position: relative;
}

.app__body {
    padding: 0 10px;
    display: flex;
    justify-content: center;
    width: 100%;
}
.topNav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    max-width: 1345px;
    width: 100%;
    z-index: 100;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    box-shadow: 0 0 2px grey;
    height: 55px;
}

.topNav__left {
    flex: 0.28 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.topNav .facebook {
    height: 45px;
    width: 45px;
    color: rgb(28, 135, 216);
}

.topNav__search {
    display: flex;
    align-items: center;
    flex: 1 1;
    padding: 0 10px;
    height: 40px;
    margin-left: 10px;
    border-radius: 50px;
    color: grey;
    background-color: #f1f2f5;
}

.topNav__search > input {
    flex: 1 1;
    width: 100%;
    padding: 9px;
    border: none;
    outline: none;
    border-radius: 50px;
    color: grey;
    font-size: 14px;
    background-color: transparent;
}

.topNav__center,
.topNav__right {
    list-style-type: none;
    display: flex;
    align-items: center;
}

.topNav__center {
    flex: 0.44 1;
    /* margin: 0 50px; */
    justify-content: space-between;
}

.topNav__center > li {
    /* border: 1px solid; */
    padding: 8px 0;
    width: 20%;
    text-align: center;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
}

.topNav__center > li:hover {
    background-color: #f1f2f5;
}


.topNav__center > li::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    width: 100%;
    height: 3px;
}

.topNav__center li:first-child::after {
    background-color: rgb(28, 135, 216);
}

.topNav__center .topNav__icon {
    color: grey;
    height: 27px;
    width: 27px;
}

.topNav__center li:first-child .topNav__icon {
    color: rgb(28, 135, 216);
}

/* .topNav__center li:nth-child(3),
.topNav__center li:last-child, */
.topNav__right {
    display: none;
}

.topNav__right {
    flex: 0.28 1;
    justify-content: space-between;
}

.topNav__right > li {
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 50%;
    background-color: #f1f2f5;
    cursor: pointer;
}

.topNav__right > li:hover {
    background-color: rgb(202, 203, 205);
}

.topNav__right .topNav__icon {
    height: 18px;
    width: 18px;
    color: black;
}

.topNav__center li,
.topNav__right li {
    position: relative;
}

.topNav__center li .after,
.topNav__right li .after {
    /* color: red; */
    position: absolute;
    bottom: -40px;
    left: 50%;
    transform: translate(-50%);
    border: 1px solid transparent;
    padding: 7px 15px;
    border-radius: 7px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #eee;
    font-size: 13px;
    text-transform: capitalize;
    visibility: none;
    display: none;
}

.topNav__center li:hover .after,
.topNav__right li:hover .after {
    visibility: visible;
    display: block;
}

