.avatar-container {
    display: flex;
    align-items: center;
    padding: 3px;
    padding-right: 10px;
    cursor: pointer;
    border-radius: 20px;
}

.avatar-container h4 {
    color: #444;
    margin-left: 5px;
    font-weight: 500;
    font-size: 13px;
}

.avatar-container .avatar {
    height:30px;
    width: 30px;
}

.avatar-container:hover {
    background-color: rgb(230, 232, 234);
}

@media screen and (min-width: 800px){
    .topNav__center {
        flex: 0.5 1;
        margin: 0 3em;
        justify-content: space-between;
    }

    .topNav__right {
        display: flex;
    }
}
.leftNav {
    flex: 0.23 1;
    width: 23%;
    margin-top: 65px;
    padding: 15px 0;
    display: flex;
    flex-direction: column;
    height: 90vh;
    overflow-y: scroll;
    position: -webkit-sticky;
    position: sticky;
    top: 65px;
}

.leftNav__item {
    display: flex;
    align-items: center;
    text-transform: capitalize;
    padding: 10px 0;
    padding-left: 10px;
    cursor: pointer;
    border-radius: 8px;
    color:red
}

.leftNav__item:hover {
    background-color: rgb(224, 227, 230);;
}

.leftNav__item > p {
    flex: 1 1;
    flex-wrap: wrap;
    overflow-wrap: break-word;
    padding-left: 10px;
    font-weight: 700;
    font-size: 14px;
    color: #333;
}

.leftNav__item .avatar {
    height: 28px;
    width: 28px;
}

.leftNav__icon {
    height: 25px;
    width: 25px;
    color: rgb(16, 115, 191);
}

/* scrollbar */
.leftNav::-webkit-scrollbar {
    width: 8px;
}

.leftNav:hover::-webkit-scrollbar-track {
    background: transparent;
}

.leftNav::-webkit-scrollbar-track:hover {
    background: rgb(221, 222, 222);
}
  
.leftNav::-webkit-scrollbar-thumb {
    /* background: #555; */
    border-radius: 30px;
}
  
.leftNav:hover::-webkit-scrollbar-thumb {
    background: rgb(183, 183, 183);
}

@media screen and (min-width: 800px){
    .leftNav {
        display: flex;
    }
}
.timeLine {
    flex: 0.54 1;
    width: 54%;
    margin: 65px 0;
    width: 100%;
    padding: 15px 5rem;
}

.timeLine__input {
    /* border: 1px solid; */
    max-width: 80%;
    margin: 0 auto;
    margin-top: 25px;
    padding: 15px;
    border-radius: 7px;
    background-color: white;
    /* box-shadow: 0 0 2px grey; */
    box-shadow: 0 2px 4px 1px #ccc;
}

hr {
    border: 1px solid #eee;
    color: #eee;
}

.input__input {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.input__input form {
    width: 100%;
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: space-between;
    border-radius: 20px;
    margin-left: 10px;
  
}

.input__input input {
    border: none;
    outline: none;
    border-radius: 20px;
    padding: 15px 20px;
    font-size: 11px;
    color: #444;
    background-color: #f1f2f5;
}

.input__input input:first-of-type {
    flex: 1 1;
    width: 70%;
}

.input__input input:last-of-type {
    background-color: #f1f2f5;
    width: 30%;
    margin-left: 10px;
    border-radius: 0;
    font-size: 11px;
}

.input__extra {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 10px;
}

.input__extra__item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 7px 0;
    width: 33%;
    /* margin: 5px; */
    border-radius: 5px;
    position: relative;
    cursor: pointer;
}

.input__extra__item:hover {
    background-color: #f1f2f5;
}

.input__extra__icon {
    height: 25px;
    width: 25px;
}

.input__extra__item > p {
    padding-left: 5px;
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 600;
    color: #444
}

.input__extra .input__extra__item:first-child {
    color: rgb(255, 0, 68);
}

.input__extra .input__extra__item:nth-child(2) {
    color: rgb(41, 188, 41);
}

.input__extra .input__extra__item:nth-child(3) {
    color: rgb(242, 181, 67)
}
.timeLine__post {
    max-width: 80%;
    margin: 15px auto 0 auto;
    border-radius: 7px;
    background-color: white;
    box-shadow: 0 2px 4px 1px #ccc;
    color: #444;
}

.timeLine__postTop {
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding-bottom: 10px;
}

.timeLine__postTopInfo {
    display: flex;
}

.timeLine__postTopUser {
    margin-left: 10px;
}

.timeLine__postTopUser >  h2 {
    font-size: 17px;
}

.timeLine__postTopUser > p {
    font-size: 11px;
    color: #aaa;
}

.timeLine__postTopDots {
    align-self: flex-start;
    border-radius: 5px;
    height: 20px;
    width: 20px;
    cursor: pointer;
}

.timeLine__postTopDots:hover {
    background-color: #ccc;
}

.timeLine__post > p {
    font-size: 16px;
    font-weight: 400;
    padding: 15px;
}

.timeLine__postImage {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* display: none; */
}

.timeLine__postImage > img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.timeLine__postButtons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-top: 1px solid #eee;
}


.timeLine__stories {
    display: flex;
    justify-content: center;
    padding: 0 30px;
}

.timeLine__story {
    /* border: 1px solid; */
    margin: 0 4px;
    height: 190px;
    flex: 0.2 1;
    width: 20%;
    /* width: 110px; */
    border-radius: 10px;
    position: relative;
    cursor: pointer;
    /* box-shadow: 0 0 5px grey; */
    box-shadow: 0 2px 4px 1px #aaa;
}

.timeLine__story img {
    position: absolute;
    object-fit: cover;
    border-radius: 10px;
    height: 100%;
    width: 100%;
    z-index: 0;
}

.timeLine__story:hover {
    transform: scale(1.02);
}

.timeLine__story::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 10px;
    height: 100%;
    width: 100%;
    z-index: 2;
}

.timeLine__story:hover::after {
    background-color: rgba(0,0,0,0.2);
}

.timeLine__story__avatar {
   border: 4px solid rgb(16, 115, 191);
   margin-left: 10px;
   margin-top: 10px;
   z-index: 12;
}

.timeLine__story > p {
    position: absolute;
    width: 100%;
    font-weight: 600;
    padding-left: 10px;
    word-wrap: break-word;
    overflow-wrap: break-word;
    bottom: 7px;
    z-index: 14;
    color: white;
}
